export const indianStates: string[] = [
  "Maharashtra",
  "Karnataka",
  "Gujarat",
  "Tamil Nadu",
];

type LocationData = {
  [state: string]: {
    [district: string]: {
      [taluka: string]: string[];
    };
  };
};

const locations: LocationData = {
  "Maharashtra": {
    "Mumbai": {
      "Mumbai": ["Mumbai"],
      "Kurla": ["Kurla"],
      "Andheri": ["Andheri"]
    },
    "Pune": {
      "Pune City": ["Pune"],
      "Haveli": ["Pimpri-Chinchwad"],
      "Maval": ["Lonavla"]
    },
    "Nagpur": {
      "Nagpur (Urban)": ["Nagpur"],
      "Nagpur (Rural)": ["Hingna"],
      "Kamthi": ["Kamthi"]
    }
  },
  "Karnataka": {
    "Bengaluru Urban": {
      "Bengaluru North": ["Bengaluru"],
      "Bengaluru East": ["K. R. Puram"],
      "Anekal": ["Anekal"]
    },
    "Mysuru": {
      "Mysuru": ["Mysuru"],
      "Hunsur": ["Hunsur"],
      "Nanjangud": ["Nanjangud"]
    }
  },
  "Gujarat": {
      "Ahmedabad": {
          "Ahmedabad City": ["Ahmedabad"],
          "Daskroi": ["Daskroi"],
          "Sanand": ["Sanand"]
      },
      "Surat": {
          "Surat City": ["Surat"],
          "Chorasi": ["Chorasi"],
          "Olpad": ["Olpad"]
      }
  },
  "Tamil Nadu": {
      "Chennai": {
          "Chennai": ["Chennai"]
      },
      "Coimbatore": {
          "Coimbatore North": ["Coimbatore"],
          "Coimbatore South": ["Coimbatore"],
          "Sulur": ["Sulur"]
      }
  }
};

export function getDistricts(state: string | undefined): string[] {
    if (!state || !locations[state]) {
        return [];
    }
    return Object.keys(locations[state]);
}

export function getTalukas(state: string | undefined, district: string | undefined): string[] {
    if (!state || !district || !locations[state] || !locations[state][district]) {
        return [];
    }
    return Object.keys(locations[state][district]);
}

export function getCities(state: string | undefined, district: string | undefined, taluka: string | undefined): string[] {
    if (!state || !district || !taluka || !locations[state] || !locations[state][district] || !locations[state][district][taluka]) {
        return [];
    }
    return locations[state][district][taluka];
}
